.toggle-button{
    display: flex;
    flex-direction: column;
    height: 25px;
    width: 36px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
    justify-content: space-around;
}
.toggle-button:focus{
    outline: none;
}

.toggle-line{
    width: 30px;
    height: 1px;
    background: white;
}