.side-drawer{
    height: 100%;
    background-color: white;
    box-shadow: 2px 0px 7px rgba(0,0,0,0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
}

.side-drawer.open{
    transform: translateX(0);
}
.side-drawer ul{
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.side-drawer li{
    margin: 0.5rem 0;
}
.side-drawer li:hover{
    color: orange;
    font-weight: bold;
}

.nav-links {
    color : #521751;
    text-decoration: none;
    font-size: 1.2rem;
}

.nav-links:hover{
    color: orange;
    font-weight: bold;
}
@media (min-width: 769px){
    .side-drawer{
        display: none;
    }
}

